.details .row {
  clear: both; }

.details .detail .search-results {
  clear: both; }

.details .related .artists {
  display: flex;
  flex-direction: column; }
  .details .related .artists .card {
    margin: 0 0 10px;
    flex-direction: row; }
    @media (max-width: 450px) {
      .details .related .artists .card {
        flex-direction: column; } }
    @media (min-width: 450px) {
      .details .related .artists .card .card-img-top {
        width: unset;
        max-width: 200px;
        max-height: 200px; } }
    .details .related .artists .card .primary-genre {
      white-space: nowrap;
      font-size: 13px; }
