.search {
  position: relative; }
  .search .search-input, .search .selected-genre {
    margin: 0 0 20px;
    clear: both; }
  .search .search-results .artists {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    overflow-x: hidden;
    max-height: 75vh; }
    .search .search-results .artists .card {
      margin: 0 0 10px;
      flex-direction: row; }
      @media (max-width: 450px) {
        .search .search-results .artists .card {
          flex-direction: column; } }
      @media (min-width: 450px) {
        .search .search-results .artists .card .card-img-top {
          width: unset;
          max-width: 200px;
          max-height: 200px; } }
      .search .search-results .artists .card .primary-genre {
        white-space: nowrap;
        font-size: 13px; }
