.saved .search-results {
  clear: both; }
  .saved .search-results .artists .card {
    margin: 0 0 10px;
    flex-direction: row; }
    @media (max-width: 450px) {
      .saved .search-results .artists .card {
        flex-direction: column; } }
    @media (min-width: 450px) {
      .saved .search-results .artists .card .card-img-top {
